.about-service {
  grid-template-columns: 1fr 1fr;
}
@media (max-width: 992px) {
  .about-service {
    flex-direction: column-reverse;
  }
}
@media (max-width: 768px) {
  .about-service__description > *:first-child {
    font-size: 1.3125rem !important;
  }
  .about-service__description > *:not(:first-child) {
    font-size: 1rem !important;
  }
}
.about-service__title {
  font-weight: 600;
}
.about-service__list {
  list-style-type: disc;
}
.about-service__image img {
  object-fit: cover;
  max-width: 100%;
  height: 100%;
}
@media (max-width: 992px) {
  .about-service__image img {
    width: 100%;
    min-height: 208px;
  }
}
.about-service.background-blur {
  width: 150%;
  height: 350%;
  opacity: 0.7;
  left: -22%;
  top: -60%;
  z-index: 0;
}
.about-service.background-blur.less {
  opacity: 0.3;
}
@media (max-width: 768px) {
  .about-service.background-blur {
    width: 250%;
    opacity: 0.4;
    left: -82%;
  }
}